.order-msg{
    width:7.5rem;
    height:2.56rem;
    background:#fff;
    margin-bottom:0.2rem;
    dl{
       width:100%;
       height:100%;
       padding:0.26rem 0.46rem 0.48rem 0.26rem;
       box-sizing:border-box;
       display:flex;
       dt{
           width:1.8rem;
           height:1.8rem;
           margin-right:0.2rem;
           img{
               width:100%;
               height:100%;
               display:block;
           }
       }
       dd{
           width:4.98rem;
           height:1.8rem;
           ul{
               width:100%;
               height:100%;
               .title-li{
                   width:100%;
                   display:flex;
                   font-size:0.28rem;
                   color:#1b1b1b;
                   margin-bottom:0.16rem;
                   .icon-span{
                       display:block;
                       width:0.66rem;
                       height:0.35rem;
                       line-height:0.35rem;
                       background:#ea9d33;
                       font-size:0.2rem;
                       color:#fff;
                       text-align:center;
                       border-radius:0.05rem;
                       margin-right:0.2rem;
                   }
               }
               .tip-li{
                   width:100%;
                   font-size:0.26rem;
                   color:#9d9d9d;
                   margin-bottom:0.1rem
               }
               .time-li{
                width:100%;
                font-size:0.26rem;
                color:#9d9d9d;
                margin-bottom:0.2rem;
               }
               .price-li{
                   width:100%;
                   font-size:0.3rem;
                   color:#ff4e4a;
                   font-weight:bolder;
               }
           }
       }
    }
}


.order-tip{
    width:7.5rem;
    background:#fff;
    border-top:0.01rem solid #d7d7d7;
    border-bottom:0.01rem solid #d7d7d7;
    margin-bottom:0.2rem;
    margin-top:0.2rem;
    p:nth-of-type(1){
        width:100%;
        height:0.82rem;
        line-height:0.82rem;
        color:#373737;
        font-size:0.26rem;
        text-indent:0.2rem;
        font-weight:bolder;
        border-bottom:0.01rem solid #d7d7d7;
    }
    p:nth-of-type(2){
        width:100%;
        line-height:0.42rem;
        font-size:0.26rem;
        color:#aaaaaa;
        margin-top:0.35rem;
        margin-bottom:0.2rem;
        margin-left:0.23rem;
    }
    p:nth-of-type(3){
        width:100%;
        font-size:0.26rem;
        color:#aaaaaa;
        margin-left:0.23rem;
        margin-bottom:0.35rem;
    }
}

.order-tel{
    width:7.5rem;
    height:0.86rem;
    background:#fff;
    border-top:0.01rem solid #d7d7d7;
    border-bottom:0.01rem solid #d7d7d7;
    margin-bottom:0.2rem;
}

.order-total{
    width:7.5rem;
    height:0.86rem;
    background:#fff;
    border-top:0.01rem solid #d7d7d7;
    border-bottom:0.01rem solid #d7d7d7;
    margin-bottom:0.2rem;
    display:flex;
    align-items: center;
    position:absolute;
    span{
        display:block;
    }
    span:nth-of-type(1){
        position:absolute;
        right:1.7rem;
        font-size:0.26rem;
        color:#3d3d3d;
        font-weight:bolder;
    }
    span:nth-of-type(2){
        position:absolute;
        right:1.36rem;
        font-size:0.2rem;
        color:#7fb4ac;
        font-weight:bolder;
    }
    span:nth-of-type(3){
        position:absolute;
        right:0.2rem;
        font-size:0.35rem;
        color:#7fb4ac;
        font-weight:bolder;
    }
}
